<template>
  <div id="app">
    <v-app id="inspire">
      <NavComponent></NavComponent>
      <div class="container">
        <div class="container hijo">
          <i class="material-icons">assignment_ind</i>

          <form>
            <v-text-field
              v-model="name"
              :error-messages="nameErrors"
              :counter="20"
              label="Nombres"
              required
              @input="$v.name.$touch()"
              @blur="$v.name.$touch()"
            ></v-text-field>

            <v-text-field
              v-model="lastname"
              :error-messages="lastnameErrors"
              :counter="20"
              label="Apellidos"
              required
              @input="$v.lastname.$touch()"
              @blur="$v.lastname.$touch()"
            ></v-text-field>

            <v-text-field
              v-model="email"
              :error-messages="emailErrors"
              label="Correo"
              required
              @input="$v.email.$touch()"
              @blur="$v.email.$touch()"
            ></v-text-field>

            <v-text-field
              v-model="password"
              :error-messages="passwordErrors"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              :type="show1 ? 'text' : 'password'"
              required
              label="Ingresa contraseña"
              :counter="20"
              @input="$v.password.$touch()"
              @blur="$v.password.$touch()"
              @click:append="show1 = !show1"
            ></v-text-field>

            <v-text-field
              v-model="age"
              :error-messages="ageErrors"
              :counter="2"
              label="Edad actual"
              required
              @input="$v.age.$touch()"
              @blur="$v.age.$touch()"
            ></v-text-field>

            <v-select
              v-model="genero"
              :error-messages="generoErrors"
              :items="Genero"
              label="Genero"
              required
            ></v-select>

            <v-select
              v-model="marital"
              :error-messages="maritalErrors"
              :items="Estados"
              label="Estado actual"
              required
            ></v-select>

            <v-select
              v-model="depend"
              :error-messages="depenErrors"
              :items="dependientes"
              label="Dependientes"
              required
            ></v-select>

            <v-text-field
              v-model="dpi"
              :error-messages="dpiErrors"
              :counter="13"
              label="Ingresa dpi"
              required
              @input="$v.dpi.$touch()"
              @blur="$v.dpi.$touch()"
            ></v-text-field>

            <v-btn class="mr-4" @click="submit" :disabled="!isComplete">submit</v-btn>
            <v-btn @click="clear">clear</v-btn>
          </form>
        </div>
      </div>
    </v-app>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, maxLength, email } from "vuelidate/lib/validators";
import axios from "axios";
import NavComponent from "../NavBarGeneral/NavComponent";
import routes from "../../main";

export default {
  name: "RegistroComponent",
  mixins: [validationMixin],
  components: {
    NavComponent
  },

  validations: {
    name: { required, maxLength: maxLength(20) },
    lastname: { required, maxLength: maxLength(20) },
    email: { required, email },
    password: { required, maxLength: maxLength(20) },
    age: { required, maxLength: maxLength(2) },
    dpi: { required, maxLength: maxLength(13) },
    marital: { required, maxLength: maxLength(7) },
    depend: { required, maxLength: maxLength(10) },
    genero:{required}
  },

  data: () => ({
    name: "",
    email: "",
    lastname: "",
    show1: false,
    age: "",
    password: "",
    dpi: "",
    marital: "",
    depend: "",
    genero:"",
    sub: true,
    Estados: ["Soltero", "Casado"],
    Genero: ["Masculino", "Fenenino","Otros"],
    dependientes: ["Independiente", "Depende de sus padres", "Otros"]
  }),

  update() {
    this.submit();
  },

  computed: {
    isComplete() {
      return (
        this.name &&
        this.lastname &&
        this.email &&
        this.password &&
        this.age &&
        this.marital &&
        this.depend &&
        this.dpi &&
        this.capErrors
      );
    },
    capErrors() {
      if (
        this.passwordErrors == "Conrtaseña es requerido." ||
        this.mail == "Correo es invalido" ||
        this.mail == "Correo es requerido" ||
        this.lastnameErrors == "Apellido es requerido." ||
        this.lastnameErrors == "No debe tener mas de 20 caracteres" ||
        this.nameErrors == "Nombre es requerido." ||
        this.nameErrors == "No debe contener mas de 20 caracteres" ||
        this.emailErrors == "Correo es invalido" ||
        this.passwordErrors == "No debe tener mas de 20 caracteres" ||
        this.dpiErrors == "Dpi es requerido." ||
        this.dpiErrors == "Tamaño maximo 13 cifras" ||
        this.ageErrors == "Este campo solo acepta numeros" ||
        this.ageErrors == "Este campo es requerido" ||
        this.ageErrors == "Tamaño maximo 2 cifras" ||
        this.generoErrors == "Genero es requerido."
      ) {
        return false;
      } else {
        return true;
      }
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("No debe contener mas de 20 caracteres");
      !this.$v.name.required && errors.push("Nombre es requerido.");
      return errors;
    },
    lastnameErrors() {
      const errors = [];
      if (!this.$v.lastname.$dirty) return errors;
      !this.$v.lastname.maxLength &&
        errors.push("No debe tener mas de 20 caracteres");
      !this.$v.lastname.required && errors.push("Apellido es requerido.");

      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Correo es invalido");
      !this.$v.email.required && errors.push("Correo es requerido");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.maxLength &&
        errors.push("No debe tener mas de 20 caracteres");
      !this.$v.password.required && errors.push("Conrtaseña es requerido.");
      return errors;
    },
    ageErrors() {
      const errors = [];
      if (!this.$v.age.$dirty) return errors;

      if (!this.age) {
        errors.push("Este campo es requerido");
      } else if (this.age.match(/[a-z-A-Z]/i)) {
        errors.push("Este campo solo acepta numeros");
      } else if (this.age.length > 2) {
        errors.push("Tamaño maximo 2 cifras");
      }

      return errors;
    },
    dpiErrors() {
      const errors = [];

      if (!this.$v.dpi.$dirty) return errors;
      if (this.dpi.length > 13) {
        errors.push("Tamaño maximo 13 cifras");
      }
      !this.$v.dpi.required && errors.push("Dpi es requerido.");

      return errors;
    },
    maritalErrors() {
      const errors = [];

      if (!this.$v.marital.$dirty) return errors;
      !this.$v.marital.required && errors.push("Estado actual es requerido.");

      return errors;
    },
    depenErrors() {
      const errors = [];

      if (!this.$v.depend.$dirty) return errors;
      !this.$v.depend.required && errors.push("Dependientes  es requerido.");

      return errors;
    },
    generoErrors() {
      const errors = [];

      if (!this.$v.genero.$dirty) return errors;
      !this.$v.genero.required && errors.push("Genero es requerido.");

      return errors;
    }
  },
  methods: {
    submit() {
      this.$v.$touch();
      this.viewData(
        this.name,
        this.lastname,
        this.age,
        this.marital,
        this.dpi,
        this.password,
        this.email,
        this.genero
      );
      this.clear();
    },
    redirect() {},
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.lastname = "";
      this.password = "";
      this.age = "";
      this.dpi = "";
      this.marital = "";
      this.depend = "";
      this.genero="";
    },
    viewData(nombre, apellido, edad, estado, id, pass, mail,genero) {
      var Data = {
        nombre: nombre,
        apellido: apellido,
        edad: edad,
        dpi: id,
        tiposCliente: {
          id_tipo_cliente: 1,
          descripcion: "A",
          promedio: 0,
          mora: 0
        },
        estadoMarital: {
          id_estado_marital: 1,
          descripcion: estado
        },
        credencial: {
          correo: mail,
          contrasena: pass,
          rol: {
            id_rol: 2,
            descripcion: "Usuario"
          }
        },
        generoPersonal: {
          id_genero: 1,
          descripcion: genero
        }
      };

      axios
        .post("http://23.251.151.234/accountmanagement/usuarios", Data)
        .then(res => {
          alert("Usuario Creado con exito");
          this.$router.push("ingresa");
        })
        .catch(error => {
          alert("Se produjo un error en la creacion de usuarios" + error);
        });
    }
  }
};
</script>

<style>
.container {
  text-align: center;
  align-content: center;
  align-items: center;
}

.container.container.hijo {
  max-width: 500px !important;

  margin-top: -30%;
}
</style>