<template>
  <div id="app">
    <v-app id="inspire">
      <NavUcomponent></NavUcomponent>

      <div class="container">
        <!--Listado usuarios-->
        <template v-if="this.dialog">
          <h4>Usuarios</h4>
          <v-data-table :headers="headers" :items="persons" class="elevation-1">
            <template v-slot:item.action="{ item }">
              <v-icon small class="mr-2" v-on:click="EditUser(item.id_usuario)">edit</v-icon>
              <v-icon small v-on:click="Duser(item.id_usuario)">delete</v-icon>
            </template>
          </v-data-table>
        </template>

        <!--Aprovar o rechazar solicitudes-->
        <template v-if="this.dialog">
          <h4>Solicitudes</h4>
          <SolicitudComponent></SolicitudComponent>
        </template>

        <!--Actualizar usuario-->
        <template v-if="!this.dialog">
          <div class="container">
            <div class="container form">
              <h3>Actualizar Usuario</h3>
              <form method="POST">
                <v-text-field
                  v-model="name"
                  :error-messages="nameErrors"
                  :counter="20"
                  label="Nombres"
                  required
                  @input="$v.name.$touch()"
                  @blur="$v.name.$touch()"
                ></v-text-field>

                <v-text-field
                  v-model="lastname"
                  :error-messages="lastnameErrors"
                  :counter="20"
                  label="Apellidos"
                  required
                  @input="$v.lastname.$touch()"
                  @blur="$v.lastname.$touch()"
                ></v-text-field>

                <v-text-field
                  v-model="dpi"
                  :error-messages="dpiErrors"
                  :counter="13"
                  label="Ingresa dpi"
                  required
                  @input="$v.dpi.$touch()"
                  @blur="$v.dpi.$touch()"
                ></v-text-field>

                <v-text-field
                  v-model="email"
                  :error-messages="emailErrors"
                  label="Correo"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.email.$touch()"
                ></v-text-field>

                <v-btn class="mr-4" @click="submit">Actualizar Usuario</v-btn>
                <v-btn @click="clear">clear</v-btn>
              </form>
            </div>
          </div>
        </template>
      </div>
    </v-app>
  </div>
</template>

<script>
import axios from "axios";
import { validationMixin } from "vuelidate";
import { required, maxLength, email } from "vuelidate/lib/validators";
import NavUcomponent from "../NavBarUsers/NavUComponent";
import SolicitudComponent from '../Solicitudes/SolicitudComponent'

export default {
  name: "AdminComponent",

  mixins: [validationMixin],
  validations: {
    name: { required, maxLength: maxLength(20) },
    lastname: { required, maxLength: maxLength(20) },
    email: { required, email },
    dpi: { required, maxLength: maxLength(13) }
  },
  components: {
    NavUcomponent,
    SolicitudComponent
  },

  mounted() {
    this.persons = [];
    this.getFrase();
  },
  data: () => ({
    persons: [],
    dialog: true,
    IdUser: 0,

    //infousernewupdate
    name: "",
    lastname: "",
    email: "",
    dpi: "",

    headers: [
      {
        text: "ID",
        align: "left",
        value: "id_usuario"
      },
      { text: "Nombre", value: "nombre" },
      { text: "Apellido", value: "apellido" },
      { text: "DPI", value: "dpi" },
      { text: "Edad", value: "edad" },
      { text: "Estado Marital", value: "estadoMarital.descripcion" },
      { text: "Correo", value: "credencial.correo" },
      { text: "Mora", value: "tiposCliente.mora" },
      { text: "Descripcion", value: "tiposCliente.descripcion" },
      {
        text: "Cuotas",
        value: "tiposCliente.clienteIntereses.0.cantidadCuotas.cantidad_Cuotas"
      },
      { text: "Actions", value: "action", sortable: false }
    ]
  }),
  update() {
    this.submit();
  },
  computed: {
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("No deve contener mas de 20 caracteres");
      !this.$v.name.required && errors.push("Nombre es requerido.");
      return errors;
    },
    lastnameErrors() {
      const errors = [];
      if (!this.$v.lastname.$dirty) return errors;
      !this.$v.lastname.maxLength &&
        errors.push("No debe tener mas de 20 caracteres");
      !this.$v.lastname.required && errors.push("Apellido es requerido.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Correo es invalid");
      !this.$v.email.required && errors.push("Correo es requerido");
      return errors;
    },
    dpiErrors() {
      const errors = [];
      if (!this.$v.dpi.$dirty) return errors;
      if (this.dpi.length > 13) {
        errors.push("Tamaño maximo 13 cifras");
      }
      return errors;
    }
  },
  methods: {
    getFrase() {
      var urlPerson = "http://23.251.151.234/accountmanagement/usuarios";
      axios.all([axios.get(urlPerson)]).then(
        axios.spread(personsRes => {
          this.persons = personsRes.data;
         
        })
      );
    },
    Duser(id) {
      //aqui se hara otra peticion para enviar el idi del usuarios y que pueda ser borrado de la base de datos
      for (var j = 0; j < this.persons.length; j++) {
        if (this.persons[j].id_usuario == id) {
          this.persons.splice(j, 1);
        }
      }
      axios
        .delete("http://23.251.151.234/accountmanagement/usuarios/" + id)
        .then(resp => {
          alert("El usuario se elimino correctamente")
        })
        .catch(response => {
          alert("Este usuario realizo un prestamo, se podra eliminar hasta que el prestamo pase al historial en 6 meses");
        });
    },
    EditUser(id) {
      this.dialog = false;
      this.IdUser = id;
      return id;
    },
    submit() {
      this.$v.$touch();
      this.viewData(this.name, this.lastname, this.dpi, this.email);
      this.clear();
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.lastname = "";
      this.email = "";
      this.dpi = "";
    },
    viewData(name, lastname, dpi, email) {
      this.dialog = true;
      var Newperson = {
        id_usuario: 0,
        nombre: name,
        apellido: lastname,
        edad: 21,
        dpi: dpi,
        tiposCliente: {
          id_tipo_cliente: 1,
          descripcion: "A",
          promedio: 20,
          mora: 5
        },
        estadoMarital: {
          id_estado_marital: 1,
          descripcion: "Soltero"
        },
        credencial: {
          id_credencial:this.IdUser ,
          correo: email,
          contrasena: "erwerwe",
          rol: {
            id_rol: 1,
            descripcion: "Usuario"
          }
        },
        generoPersonal: {
          id_genero: 1,
          descripcion: "Masculino"
        }
      };

alert(this.IdUser)
      axios
        .put(
          "http://23.251.151.234/accountmanagement/usuarios/" +
            this.IdUser,
          Newperson
        )
        .then(resp => {
          if (resp.status == 200) {
            window.location.reload();
          }
        })
        .catch(() => {
          alert("Algo inesperado sucedio")
        });
    }
  }
};
</script>

<style>
.container {
  text-align: center;
  max-width: 700px !important;
  padding-top: 150px !important;
}

.container .form {
  max-width: 400px !important;
}
</style>