<template>
  <div id="app">
    <v-app>
      <v-data-table :headers="headers" :items="persons" class="elevation-1">
        <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" v-on:click="Aprovar(item.id_prestamo)">thumb_up</v-icon>
          <v-icon small v-on:click="Rechazar(item.id_prestamo)">thumb_down</v-icon>
        </template>
      </v-data-table>

      <ul id="example-1">
        <li v-for="item in ganancia" v-bind:key="item.id">{{ item.message }}</li>
      </ul>

    </v-app>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SolicitudComponent",
  data() {
    return {
      headers: [
        {
          text: "ID",
          align: "left",
          value: "id_prestamo"
        },
        { text: "Usuario", value: "id_usuario" },
        { text: "Fecha de solicitud", value: "fecha_inicio" },
        { text: "Fecha Cancelacion", value: "fecha_final" },
        { text: "Monto solicitado", value: "monto_solicitado" },
        { text: "Monto con intereses", value: "monto_real" },
        { text: "Estado", value: "estado" },
        { text: "Descripcion", value: "descripcion" },
        { text: "Actions", value: "action", sortable: false }
      ],

      persons: [],
      ganancia: [],
      maxLenght: 0
    };
  },

  methods: {
    Aprovar(id) {
      let estado = {
        estado: "A"
      };

      let url = "http://23.251.151.234/loanmanagement/prestamo/estado/";
      axios
        .put(url + id, estado)
        .then(resp => {
          alert("Se aprovo con exito");
        })
        .catch(() => {
          alert("Algo salio mal ahorita no se puede procesar su peticion");
        });
    },
    Rechazar(id) {
      let estado = {
        estado: "R"
      };
      let url = "http://23.251.151.234/loanmanagement/prestamo/estado/";
      axios
        .put(url + id, estado)
        .then(resp => {
          alert("El credito fue rechazado con exito");
        })
        .catch(() => {
          alert("Algo salio mal ahorita no se puede procesar su peticion");
        });
    },

    getSolicitud() {
      var urlSolicitud = "http://23.251.151.234/loanmanagement/listar";
      axios.all([axios.get(urlSolicitud)]).then(
        axios.spread(solicitudRes => {
          this.maxLenght = solicitudRes.data.length;
          this.persons = solicitudRes.data;
        })
      );
    },
  },
  mounted() {
    this.getSolicitud();
  }
};
</script>

