<template>
  <div id="app">
    <v-app id="inspire">
      <form method="POST">
        <v-text-field
          v-model="dinero"
          :error-messages="dineroErrors"
          :counter="4"
          :append-icon="'monetization_on'"
          label="Q.0000"
          required
          @input="$v.dinero.$touch()"
          @blur="$v.dinero.$touch()"
        ></v-text-field>

        <v-textarea
          v-model="textArea"
          :error-messages="areaErrors"
          :counter="125"
          solo
          name="input-7-4"
          label="Ingresa el motivo de tu solicitud de dinero maximo 125 caracteres"
          @input="$v.textArea.$touch()"
          @blur="$v.textArea.$touch()"
        ></v-textarea>

        <v-select
          v-model="OptionSelect"
          :error-messages="selectError"
          :items="items"
          label="Seleccion en cuantas cuotas quiere pagar"
          required
        ></v-select>
        <v-btn class="mr-4" @click="submit" :disabled="!isComplete">Solicitar</v-btn>
      </form>
    </v-app>
  </div>
</template>


<script>
import { validationMixin } from "vuelidate";
import { required, maxLength } from "vuelidate/lib/validators";
import axios from "axios";
import routes from "../../main";

export default {
  name: "FormMoneyComponent",

  mixins: [validationMixin],

  validations: {
    dinero: { required, maxLength: maxLength(5) },
    OptionSelect: { required },
    textArea: { required, maxLength: maxLength(125) }
  },
  data: () => {
    return {
      formulario: true,
      dinero: "",
      items: ["12"],
      OptionSelect: "",
      info: [],
      userInfo: [],
      textArea: ""
    };
  },
  computed: {
    isComplete() {
      return this.dinero && this.capErrors;
    },
    capErrors() {
      if (
        this.dineroErrors == "No debe tener mas de 5 caracteres." ||
        this.dineroErrors == "Monto es requerido." ||
        this.dineroErrors == "Este campo solo puede contener numero." ||
        this.selectError == "Cuotas es requerido." ||
        this.areaErrors == "Motivo de solicitud es requerido." ||
        this.areaErrors == "Este campo solo puede contener letras." ||
        this.areaErrors == "Tiene que tener una descripcion obligatoria mayor a 50 caracteres" ||
        this.areaErrors == "Tiene que ingresar una descripcion" 
      ) {
        return false;
      } else {
        return true;
      }
    },

    areaErrors() {
      const errors = [];

      if (!this.$v.textArea.$dirty) return errors;

      if (!this.textArea.match(/[a-z-A-Z]/i)) {
        errors.push("Este campo solo puede contener letras.");
      } else if (this.textArea.length < 50 || this.textArea.length > 125) {
        errors.push("Tiene que tener una descripcion obligatoria mayor a 50 caracteres");
      } else if (this.textArea.length == 0) {
        errors.push(
          "Tiene que ingresar una descripcion"
        );
      }

      return errors;
    },
    dineroErrors() {
      const errors = [];
      if (!this.$v.dinero.$dirty) return errors;

      if (!this.dinero.match(/^-?\d*\.?\d+$/)) {
        errors.push("Este campo solo puede contener numero.");
      } else if (this.dinero.length > 4) {
        errors.push("Este campo solo puede tene 5 caracteres");
      }
      return errors;
    },
    selectError() {
      const errors = [];
      !this.$v.OptionSelect.required && errors.push("Cuotas es requerido.");
      return errors;
    }
  },
  methods: {
    submit() {
      this.$v.$touch();
      this.SendData(this.dinero, this.OptionSelect, this.textArea);
      this.$router.push("panel");

      //   var id = JSON.parse(localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED"));
      this.getQuotes();
      this.clear();
    },
    clear() {
      this.$v.$reset();
      this.dinero = "";
      this.OptionSelect = "";
      this.textArea = "";
    },
    getQuotes() {
      axios
        .get("http://23.251.151.234/cataloguemanagement/quotas")
        .then(resp => {
          this.items = resp.data;
        })
        .catch(() => {
          this.$router.push("error");
        });
    },
    SendData(dinero, selectoption, area) {
      if (
        JSON.parse(localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED")) === null
      ) {
        alert("Usted no puede estar aca");
        //descomentar esta linea importante
        // this.$router.push("/");
      } else {
        this.userInfo = JSON.parse(
          localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED")
        );
      }

      let prestamoData = {
        id_usuario: this.userInfo.id_usuario,
        id_cantidad_cuota: "12",
        estado: "P",
        monto_solicitado: dinero,
        descripcion: area
      };
      //aqui vamos a enviar la solicitud del prestamo que luego nos va
      // a reidireccional a nuestro panel que se va a habilitar
      // hasta que sea aprovado nuestro  prestamo

      axios
        .post(
          "http://23.251.151.234/loanmanagement/prestamo",
          prestamoData
        )

        .then(resp => {
          if ((resp.status = 200)) {
            alert("Su solicitud  se ingreso con exito");
          }
        })
        .catch(() => {
          alert("Algo salio mal vuelve a intentarlo mas tarde");
        });
    }
  }
};
</script>

