import Home from './views/Home.vue'
import RegistroComponent from './components/RegistroUsuario/RegistroComponent.vue'
import IngresaComponent from './components/LogIn/ingresa'
import AdminComponent from './components/Administrador/admin'
import UsuarioComponent from './components/Usuario/usuario.vue'
import PanelComponent from './components/panelUsuario/PanelComponent.vue'
import ComponentError from './components/erroPage/pageError.vue'
const routes = [

    { path: '/', component: Home, name: 'home' },
    { path: '/registro', component: RegistroComponent, name: 'registro' },
    { path: '/ingresa', component: IngresaComponent, name: 'ingresa' },
    { path: '/admin', component: AdminComponent, name: 'admin' },

    { path: '/usuario', name: 'usuario', component: UsuarioComponent, },
    { path: '/panel', component: PanelComponent, name: 'panel' },
    { path: '/error', component: ComponentError, name: 'error' }

];

export default routes;