<template>
  <div id="app">
    <v-app id="inspire">
      <NavComponent></NavComponent>

      <div class="container up">
        <div class="container down">
          <h3>Inicia Sesion</h3>
          <form method="POST">
            <v-text-field
              v-model="email"
              :error-messages="emailErrors"
              label="Correo"
              required
              @input="$v.email.$touch()"
              @blur="$v.email.$touch()"
            ></v-text-field>

            <v-text-field
              v-model="password"
              :error-messages="passwordErrors"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              :type="show1 ? 'text' : 'password'"
              required
              label="Ingresa contraseña"
              :counter="20"
              @input="$v.password.$touch()"
              @blur="$v.password.$touch()"
              @click:append="show1 = !show1"
            ></v-text-field>

            <v-btn class="mr-4" @click="submit" :disabled="!isComplete">submit</v-btn>
            <v-btn @click="clear">clear</v-btn>
          </form>
        </div>
      </div>
    </v-app>
  </div>
</template>


<script>
import NavComponent from "../NavBarGeneral/NavComponent";
import { validationMixin } from "vuelidate";
import { required, maxLength, email } from "vuelidate/lib/validators";
import axios from "axios";
import routes from "../../main";

export default {
  name: "IngresaComponent",
  mixins: [validationMixin],
  components: {
    NavComponent
  },
  validations: {
    email: { required, email },
    password: { required, maxLength: maxLength(20) }
  },

  data: () => ({
    email: "",
    show1: false,
    password: "",
    cap: false,
    _UserLogin: [],
    other: "all"
  }),

  update() {
    this.submit();
  },
  computed: {
    isComplete() {
      return this.email && this.password && this.capErrors;
    },
    capErrors() {
      if (
        this.emailErrors == "Correo es invalido" ||
        this.emailErrors == "Correo es requerido" ||
        this.passwordErrors == "No debe tener mas de 20 caracteres" ||
        this.passwordErrors == "Contraseña es requerido."
      ) {
        return false;
      } else {
        return true;
      }
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Correo es invalido");
      !this.$v.email.required && errors.push("Correo es requerido");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.maxLength &&
        errors.push("No debe tener mas de 20 caracteres");
      !this.$v.password.required && errors.push("Contraseña es requerido.");
      return errors;
    }
  },

  methods: {
    submit() {
      this.$v.$touch();
      this.viewData(this.password, this.email);
      this.clear();
      this.cap = true;
    },

    clear() {
      this.$v.$reset();
      this.email = "";
      this.password = "";
    },
    viewData(pass, mail) {
      var LogInUser = {
        id_credencial: 0,
        correo: mail,
        contrasena: pass
      };

      axios
        .post("http://23.251.151.234/accountmanagement/login", LogInUser)
        .then(res => {
          this._UserLogin = res.data;

          if (
            this._UserLogin.credencial.rol.id_rol == 1 &&
            this._UserLogin.credencial.correo == LogInUser.correo &&
            LogInUser.correo == this._UserLogin.credencial.correo &&
            LogInUser.contrasena == this._UserLogin.credencial.contrasena
          ) {
            this.$router.push("admin");
          } else if (
            this._UserLogin.credencial.rol.id_rol == 2 &&
            this._UserLogin.credencial.correo == LogInUser.correo &&
            LogInUser.correo == this._UserLogin.credencial.correo &&
            LogInUser.contrasena == this._UserLogin.credencial.contrasena
          ) {
            this.$router.push("usuario");
            localStorage.setItem(
              "ICON_DAT_UNIQUE_RESOLVED",
              JSON.stringify(this._UserLogin)
            );
          }
        })
        .catch(() => {
          alert("Usuario y contraseña son invalidos")
          this.$router.push("/");
        });
    }
  }
};
</script>



<style>
.container.up {
  text-align: center;
  align-content: center;
  align-items: center;
  padding-top: 99px !important;
}

.container.down {
  max-width: 400px !important;

  padding: 0px 0px 235px 0px;
}

.container .label {
  max-height: 100px !important;
}
</style>