
<template>
  <div id="app">
    <v-app id="inspire">
      <NavUcomponent></NavUcomponent>
      <v-container>
        <!-- Card aprovacion -->
        <div v-if="PanelHand">
          <template v-if="Panel">
            <v-data-table :headers="headers" :items="individualPerson" class="elevation-1">
              <template v-slot:item.action="{ item }">
                <v-icon small class="mr-2" @click="TarjetCredito(item.id_cuota)">call_to_action</v-icon>
              </template>
            </v-data-table>
          </template>

          <template v-else>
            <form method="POST">
              <v-text-field
                v-model="tarjeta"
                :error-messages="dineroErrors"
                :counter="16"
                label="Ingrese su numero de tarjeta de credito"
                required
                @input="$v.tarjeta.$touch()"
                @blur="$v.tarjeta.$touch()"
              ></v-text-field>
              <v-btn class="mr-4" @click="submit" :disabled="!isComplete">Solicitar</v-btn>
            </form>
          </template>
        </div>

        <div v-else>
          <v-card
            class="mx-auto"
            :flat="flat"
            :loading="loading"
            :outlined="outlined"
            :elevation="elevation"
            :raised="raised"
            :width="width"
            :height="height"
          >
            <v-img v-if="media" class="white--text" height="200px" src="../../assets/21501188.jpg"></v-img>
            <!--Si esta pendinete-->

            <v-card-text>Espere unos minutos en lo que carga el resultado de su peticion.</v-card-text>
            <v-card-text v-if="!PP" class="yellow">Su credito esta en espera.</v-card-text>

            <!--Si es aprovado-->
            <v-card-text
              v-if="!AA"
              :style="{color}"
              class="green"
            >Su credito fue aprovado, espere unos minutos en lo que carga su dashboard</v-card-text>
            <!--Si es rechazado-->
            <v-card-text v-if="!RR" :style="{color}" class="red">Su credito fue rechazado</v-card-text>

            <v-card-text
              v-if="!AP"
              :style="{color}"
              class="grey"
            >Su credito aun no ha sido procesado recuerda que puede tardar unas cuantas horas o no tienes ninguna solicitud de credito</v-card-text>
            <v-card-actions v-if="actions"></v-card-actions>
          </v-card>
        </div>
        <!-- Mostrar todos los pagos a realizar -->

        <!-- Pagar Cuota -->
      </v-container>
    </v-app>
  </div>
</template>

<script>
import axios from "axios";
import NavUcomponent from "../NavBarUsers/NavUComponent";
import routes from "../../main";
import { required, maxLength } from "vuelidate/lib/validators";
import { validationMixin } from "vuelidate";

export default {
  name: "PanelComponent",
  mixins: [validationMixin],
  validations: {
    tarjeta: { required, maxLength: maxLength(16) }
  },
  components: {
    NavUcomponent
  },
  data: () => {
    return {
      //configuraciones card
      tarjeta: "",
      color: "white",
      flat: false,
      media: true,
      loading: false,
      actions: true,
      outlined: false,
      elevation: undefined,
      raised: false,
      width: 344,
      height: undefined,
      PanelHand: false,
      userInfo: [],

      // fin configuracion card
      individualPerson: [],
      headers: [
        { text: "ID", align: "left", value: "cuota_num" },
        {
          text: "Monto a pagar(Cifras expresadas en Quetzales)",
          value: "monto"
        },
        { text: "Fecha limite de pago", value: "fecha_limite_pago" },
        { text: "Estado", value: "estado" },

        { text: "Realizar Pago", value: "action", sortable: true }
      ],
      Cuotas: [],
      AA: true,
      RR: true,
      PP: true,
      AP: true,
      Panel: true,
      IDUSUAIROPATH: 0
    };
  },
  computed: {
    isComplete() {
      return this.tarjeta && this.capErrors;
    },
    capErrors() {
      if (
        this.dineroErrors == "Este campo solo puede tener 16 caracteres" ||
        this.dineroErrors == "Numero de tarjeta es requerido." ||
        this.dineroErrors == "Este campo solo puede contener numeros."
      ) {
        return false;
      } else {
        return true;
      }
    },
    dineroErrors() {
      const errors = [];
      if (!this.$v.tarjeta.$dirty) return errors;

      if (!this.tarjeta.match(/^-?\d*\.?\d+$/)) {
        errors.push("Este campo solo puede contener numeros.");
      } else if (this.tarjeta.length > 16) {
        errors.push("Este campo solo puede tener 16 caracteres");
      }
      return errors;
    }
  },

  methods: {
    getData() {
      //Agregar esta linea ---> a la request this.userInfo.id_usuario

      if (
        JSON.parse(localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED")) === null
      ) {
        alert("Usted no puede estar aca");
        //descomentar esta linea importante
        // this.$router.push("/");
      } else {
        this.userInfo = JSON.parse(
          localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED")
        );

        //agregar esta linea -->  this.userInfo.id_usuario
        axios
          .get("http://23.251.151.234/loanmanagement/usuarios/" + this.userInfo.id_usuario)
          .then(resp => {
            this.individualPerson = resp.data.cuotas;
        
            if (resp.status == 200) {
              if (resp.data.estado == "A") {
                return (
                  alert("Felicidades su credito fue aprovado"),
                  (this.PanelHand = true)
                );
                //Cambia el spam a otro mensaje de activacion
              } else if (resp.data.estado == "P") {
                //El spam se mantiene
                return (this.PP = false);
              } else if (resp.data.estado == "R") {
                //El spam tendria que cambiar de mensaje
                return (this.RR = false);
              }
            }
          })
          .catch(() => {
            return (this.AP = false);
          });
      }
    },
    TarjetCredito(id) {
      this.IDUSUAIROPATH = id;
      this.Panel = false;
    },
    PagarCredito(NumberTarjeta) {
      var dataCredit = {
        tarjeta_credito: NumberTarjeta
      };

      axios
        .put(
          "http://23.251.151.234/loanmanagement/prestamo/cuota/" +
            this.IDUSUAIROPATH,
          dataCredit
        )
        .then(() => {
          alert("Su pago se realizo con exito pueden pasar unos minutos hasta que se  actualice su estado")
        })
        .catch(() => {
          alert("Algo salio mal vuelve a interntarlo mas tarde");
        });
    },
    submit() {
      this.$v.$touch();
    
      this.PagarCredito(this.tarjeta);
      this.clear();

      this.Panel = true;
    },
    clear() {
      this.$v.$reset();
      this.tarjeta = "";
    }
  },
  mounted() {
    this.getData();
  }
};
</script>


<style>
.title {
  color: rgb(255, 255, 255);
}
</style>