<template>
  <div id="app">
    <v-app id="inspire">
      <NavUcomponent></NavUcomponent>
      <div class="container">
        <div class="tarjet" v-if="panel">
          <v-card
            class="mx-auto"
            :flat="flat"
            :loading="loading"
            :outlined="outlined"
            :elevation="elevation"
            :raised="raised"
            :width="width"
            :height="height"
          >
            <v-img v-if="media" class="white--text" height="200px" src="../../assets/2147878.jpg">
              <v-card-title class="title align-end fill-height">¿Deseas solicitar un credito?</v-card-title>
            </v-img>
            <div v-if="spam">
              <span class="colorSpam">usted tiene un credito pendiente</span>
            </div>
            <v-card-text>Recuerda que somos tu mejor opcion en microcreditos, con las tarifas mas bajas y dinero al instante.</v-card-text>
            <v-card-actions v-if="actions">
              <v-btn :disabled="Bdisable" outlined @click="Fsi()">Si</v-btn>
              <v-btn :disabled="BlockPanel" outlined @click="Fpanel()">Ir a mi panel</v-btn>
            </v-card-actions>
          </v-card>
        </div>

        <!--Este es el campo de formulario para solicitar dinero-->
        <div class="container formulario" v-if="!formulario">
          <FormMoneyComponent></FormMoneyComponent>
        </div>
      </div>
    </v-app>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, maxLength } from "vuelidate/lib/validators";
import NavUcomponent from "../NavBarUsers/NavUComponent";
import FormMoneyComponent from "../FormMoney/FormMoney";
import axios from "axios";

import routes from "../../main";

export default {
  name: "UsuarioComponent",
  components: {
    NavUcomponent,
    FormMoneyComponent
  },
  mixins: [validationMixin],
  validations: {
    dinero: { required, maxLength: maxLength(5) },
    OptionSelect: { required }
  },

  data: () => {
    return {
      flat: false,
      media: true,
      loading: false,
      actions: true,
      outlined: false,
      elevation: undefined,
      raised: false,
      width: 344,
      height: undefined,
      panel: true,
      formulario: true,
      dashboard: true,
      Bdisable: true,
      userInfo: [],
      persons: [],
      spam: false,
      BlockPanel:true

    };
  },
  props: {},
  methods: {
    Fsi() {
      this.formulario = false;
      this.panel = false;
    },
    Fpanel() {
      this.$router.push("panel");
    },
    Verification() {
      
      this.userInfo = JSON.parse(
        localStorage.getItem("ICON_DAT_UNIQUE_RESOLVED")
      );

      //esto se le va concatenar
      var urlPerson = "http://23.251.151.234/loanmanagement/usuarios/";
      axios
      //Agregar esta linea: this.userInfo.id_usuario
        .all([axios.get(urlPerson + this.userInfo.id_usuario)])
        .then(
          axios.spread(personsRes => {
            if (personsRes.status == 200) {
              this.persons = personsRes.data;
              if (this.persons.estado == "A") {

                this.spam = true;
                this.Bdisable = true;
                this.BlockPanel = false;

              } else if (this.persons.estado == "P") {
                
                this.Bdisable = true;
                this.spam = true;
                this.BlockPanel = false;
             
              } else if (this.persons.estado == "F") {
                
                this.Bdisable = false;
                this.spam = false;
               this.BlockPanel = true;
      
              }
            }
          })
        )
        .catch(() => {
          this.Bdisable = false;
          this.spam = false;
          this.BlockPanel = true;
       
        });
    }
  },
  mounted() {
    this.Verification();
  }
};
</script>

<style>
.formulario {
  padding-top: 8%;
}
.title {
  color: black;
}

.container {
  background-color: transparent;
}

.tarjet {
  padding-top: 8%;
}

.container.formulario {
  padding-top: 15% !important;
  max-width: 400px !important;
}

.colorSpam {
  color: red;
}
</style>
